<template>
	<div class="container BorderX">		
		<div class="content">
			<h1>Trade or see your Porfolio</h1>
			<h6>Click 'End Day' to begin the new day</h6>
			<h6>You can Save and Load your Trade to the server</h6><hr>
			<h2>Your fund: ${{ myFund }}</h2>		
		</div>
			
	</div>
	
</template>

<script>
	export default {
		computed:{
			myFund(){
				return this.$store.getters.myFund;
			}
		}
	}
</script>

<style scoped>
	.BorderX{
		border: solid 1px #bfbfbf;
		border-radius: 8px;
	}
	.content{
		padding: 15px;
	}
</style>


