<template>
	<div class="container">
		<div class="row">
			<stock-unit v-for="companystock in AllStocks" :companystock="companystock">
			
			</stock-unit>
		</div>
		
	</div>
	
</template>

<script>
	import stockUnit from './Unit/StockUnit.vue'
	export default {
		components:{
			stockUnit,
		},
		computed:{
			AllStocks(){
				return this.$store.getters.allStocks;
			}
		},
				
	}
</script>



