<template>
    <div class="container"><br><br>

		<app-header></app-header><hr>
		<transition name="slide" mode="out-in">
			<router-view></router-view>
		</transition>		
    </div>
</template>

<script>
	import appHeader from './components/Header.vue'
	import storeData from './data/storeData.js'
    export default {
    	components:{
    		appHeader,
    	},
    	created(){
			this.$store.dispatch('initStock',storeData)
			this.$store.dispatch('randomStockPrice')
		}
    }
</script>

<style>
	.slide-enter-active{
		animation: slideUpIn 0.7s ease-in ;
	}
	.slide-leave-active{
		animation: slideUpOut 0.7s ease-in ;
	}
	@keyframes slideUpIn {
		from{
			opacity: 0;
			transform: translateY(-20px);
		}
		to{
			opacity: 1;
			transform: translateY(0px);
		}
	}
	@keyframes slideUpOut {
		from{
			opacity: 1;
			transform: translateY(0px);
		}
		to{
			opacity: 0;
			transform: translateY(-20px);
		}
	}
</style>



