

const state={
	allStocks:[],
	
}

const mutations={
	initStock: (state,storeData)=>{
		state.allStocks=storeData;
	},
	randomStockPrice: (state)=>{
		state.allStocks.forEach(ele=>{
			var x=Math.random()
			if(x>0.5){
				ele.price=ele.price+Math.floor(Math.random()*20+1)
			}			
			else {
				if(ele.price<=0){
					ele.price=Math.floor(Math.random()*20+1)
				} else {
					ele.price=ele.price-Math.floor(Math.random()*20+1)
				}				
			}
		})
	}
}

const actions={
	initStock: ({commit},storeData)=>{
		commit('initStock',storeData);
	},

	buyStock({commit},order){
		commit('buyStock',order)
	},

	randomStockPrice({commit}){
		commit('randomStockPrice')
	}
}

const getters={ 
	allStocks:(state)=>{
		return state.allStocks;
	},

}

export default{
	state,
	mutations,
	actions,
	getters,
	
}


