<template>
	<div class="container">
		<div class="row">
			<PorfolioUnit v-for="porfolioStock in porfolioStocks" :porfolioStock="porfolioStock"></PorfolioUnit>
		</div>
		
	</div>
	
</template>

<script>
	import PorfolioUnit from './Unit/PorfolioUnit.vue';
	export default {
		components:{
			PorfolioUnit
		},
		computed:{
			porfolioStocks(){
				return this.$store.getters.porfolioStocks;
			}
		}
	}
</script>


