
<template>
	
		<ul class="list-group col-md-6 Box">
		  <li class="list-group-item title">{{ companystock.name }} 
		  	<small class="titlePri">(Price: {{ companystock.price }}) </small>
		  </li>
		  <li class="list-group-item info">
		  	<div class="row">
		  		
		  	<input type="number" class="form-control col-md-6" placeholder="Place price here" v-model.number="quantity">
		  	<button class="btn btn-primary " 
		  			style="margin-left: 10px;"
		  			:disabled="quantity<=0 || !Number.isInteger(quantity) || checkNotEnoughMoney" 
		  			@click="buyStock"> {{checkNotEnoughMoney? 'Not Enough': 'Buy'}} 
		  	</button>
		  	</div>
		  </li>
		</ul>
	
</template>

<script>
	export default {
		data: function(){
			return{
				quantity: 0,
			}
		},
		computed:{
			checkNotEnoughMoney(){
				return this.quantity*this.companystock.price>this.$store.getters.myFund
			}
		},
		props:['companystock'],
		methods:{
			buyStock(){
				const order={
					id: this.companystock.id,
					quantity: this.quantity,
					price: this.companystock.price
				}
				console.log(order);
				this.$store.dispatch('buyStock',order)
				this.quantity=0;
			}
		}
	}
	
</script>

<style scoped>
	.title{
		background: #aff8ff;
		color: #003c71;
		font-weight: bold;
		font-size: 25px
	}
	.Box{
		margin-bottom: 10px;
	}
	.info{
		
	}
</style>


