<template>
	<div >
		<nav class="navbar navbar-expand-lg navbar-light bg-light">
	 		<router-link tag="a" class="navbar-brand" :to="{name: 'HomePage' }">
	      	Stock Trader
	      </router-link>
	  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
	    <span class="navbar-toggler-icon"></span>
	  </button>

	  <div class="collapse navbar-collapse" id="navbarSupportedContent">
	    <ul class="navbar-nav mr-auto">      

	      <router-link tag="li" class="nav-item" active-class="active" :to="{name: 'Porfolio'}" exact>
	      	 <a class="nav-link">Porfolio</a>
	      </router-link>

	       <router-link tag="li" class="nav-item" active-class="active" :to="{name: 'Stock'}">
	      	 <a class="nav-link">Stock</a>
	      </router-link>	        


	      <span class="navbar-text my-2 my-sm-0"
	      style="font-weight: bold; color:#006090"> ${{ myFund  }}</span>	      
	       
	    </ul>
	     <form class="form-inline my-2 my-lg-0">

	      <div class="nav-item dropdown ">
	        <a class="nav-link dropdown-toggle dropdownX" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
	          Save and Load
	        </a>
	        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
	          <a class="dropdown-item" href="#" @click="saveData">Save</a>
	          <a class="dropdown-item" href="#" @click="loadData">Load</a>
	        </div>
	      </div>
	      <button class="btn btn-warning" href="#" @click.prevent="endDay"> 
	      	End Day 
	      </button>

	    </form>
	     
	  </div>
	</nav>
	</div>
</template>

<script>
	export default {
		data: function(){
			return{
				theResource: '',
				theResource2: '',
			}
		},
		computed:{
			myFund(){
				return this.$store.getters.myFund;
			},
			saveDataStock(){
				return this.$store.getters.porfolioStocks;
			}
		},
		methods:{
			endDay(){
				this.$store.dispatch('randomStockPrice')
			},
			saveData(){
				this.theResource.update(this.saveDataStock)
				 .then(response=>{
				 	console.log(response);
				 },error=>{
				 	console.log(error);
				 })
				this.theResource2.update(this.myFund)
				.then(response=>console.log(response))
			},
			loadData(){
				var oldData=[];
				var oldFund=0;
				this.theResource.get().then(response=>{
					return response.json()
					console.log(response);
				}) .then(data=>{
					oldData=data;
					this.$store.dispatch('loadData',oldData)
				})
				this.theResource2.get().then(response=> {return response.body})
				.then(data=>{
					oldFund=data;
					this.$store.dispatch('loadOldFund',oldFund)
				})

			}
		},
		created(){
			this.theResource=this.$resource('https://http-vuejs-59db2.firebaseio.com/Stock.json')
			this.theResource2=this.$resource('https://http-vuejs-59db2.firebaseio.com/Fund.json')
		}
	}
</script>

<style scoped>
	.navbar{
		border: solid 1px #d6d6d6;
		border-radius: 5px;
	}
	.endDay{
		color: #d6d6d6 !important;
		font-weight: bold;
	},
	.dropdownX{
		color: gray;
	}
	.btn-warning{
		color: black;
	}
</style>


