
const state={
	porfolioStocks:[],
	myFund: 10000
}

const mutations={

	buyStock: (state,{id,price,quantity})=>{
		var record= state.porfolioStocks.find(ele=>ele.id==id);
		if(record){
			record.quantity+=quantity;
		} else {
			state.porfolioStocks.push({
				id: id,
				quantity: quantity
			})
		}
		state.myFund-=quantity*price
	},
	
	sellStock: (state,{id,quantity2,price}) =>{
		var record= state.porfolioStocks.find(ele=>ele.id==id);
		if(quantity2<record.quantity){
			record.quantity-=quantity2;
		} else {
			state.porfolioStocks.splice(state.porfolioStocks.indexOf(record),1);
		}
		state.myFund+=quantity2*price
	},
	loadData(state,oldData){
		state.porfolioStocks=oldData;
	},
	loadOldFund(state,OldFund){
		state.myFund=OldFund;
	}
}

const actions={
	sellStock({commit}, orderSell){
		commit('sellStock',orderSell)
	},
	loadData({commit},oldData){
		commit('loadData',oldData)
	},
	loadOldFund({commit},OldFund){
		commit('loadOldFund',OldFund)
	}
}

const getters={
	porfolioStocks (state,getters){
		 return state.porfolioStocks.map(porStock=>{
			var record = getters.allStocks.find(stock=> porStock.id == stock.id)
			return {
				id: porStock.id,
				name: record.name,
				price: record.price,
				quantity: porStock.quantity
			}
		})
	},
	myFund: state=>{
		return state.myFund;
	}
}

export default{
	state,
	mutations,
	actions,
	getters,
}

