<template>
	
		<ul class="list-group col-md-6 Box">
		  <li class="list-group-item title">{{ porfolioStock.name }} 
		  	<small class="titlePri">(Price: {{ porfolioStock.price }} | 
		  	 quantity: {{porfolioStock.quantity }}) </small>
		  </li>
		  <li class="list-group-item info">
		  	<div class="row">
		  		
		  	<input type="number" class="form-control col-md-6" placeholder="Place price here" v-model.number="quantity2">
		  	<button class="btn btn-danger " 
		  			style="margin-left: 10px;"
		  			:disabled="quantity2<=0 || !Number.isInteger(quantity2) || checkNotEnoughQuantity"
		  			@click="sellStock"> {{checkNotEnoughQuantity? 'Not Enough' : 'Sell'}} 
		  	</button>
		  	</div>
		  </li>
		</ul>
	
</template>

<script scoped>
	export default {
		props:['porfolioStock'],
		data: function(){
			return{
				quantity2: 0
			}		
		},
		computed:{
			checkNotEnoughQuantity(){
				return this.quantity2>this.porfolioStock.quantity;
			}
		},
		methods: {
			sellStock(){
				const orderSell={
					id: this.porfolioStock.id,
					quantity2: this.quantity2,
					price: this.porfolioStock.price,
				};
				this.$store.dispatch('sellStock',orderSell)
				this.quantity2=0;
			}
		}
	}
</script>

<style >
	.title{
		background: #F26F8F;
		color: white;
		font-weight: bold;
		font-size: 25px
	}
	.Box{
		margin-bottom: 10px;
	}
	.info{
		
	}
</style>


